
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import { mapGetters } from 'vuex'
import { getUserInfo, removeToken, removeUserInfo } from '../assets/js/auth'
import {baseURL} from '../assets/js/common'

export default {
name: 'Layout',
directives: {
TransferDom
},
components: {
Tabbar,
TabbarItem,
ViewBox,
XHeader,
XSwitch,
Popup,
CellFormPreview,
Group,
Cell,
XInput,
XButton,
Flexbox,
FlexboxItem,
Divider,
Checker,
CheckerItem,
Confirm
},
methods: {

onConfirm () {
if (this.confirmItem != null) {
let data = {
code: 1,
item: this.confirmItem
}
this.websocketsend(JSON.stringify(data))
}
},
itemClick (item) {
this.showConfirm = true
this.confirmItem = item
console.log('itemClick' + JSON.stringify(item))
},
clickMore () {
if (this.show) {
this.show = false
} else {
this.show = true
}
},
doLogout () {
removeToken()
removeUserInfo()
this.$router.push({ path: '/' })
this.$vux.toast.show({
text: '退出成功',
position: 'middle',
type: 'text'
})
},
/*
*
*/
initWebSocket () { // 初始化weosocket
let userinfo = getUserInfo()
let username = userinfo.waiter_userid

this.websock = new WebSocket('ws://' + baseURL + '/websocket/' + username)

this.websock.onmessage = this.websocketonmessage
this.websock.onerror = this.websocketonerror
this.websock.onopen = this.websocketonopen
this.websock.onclose = this.websocketclose
},
websocketonopen () { // 连接建立之后执行send方法发送数据
this.$vux.toast.show({
text: 'WebSocket连接成功',
position: 'middle',
type: 'success'
})
let data = {
code: 0,
msg: '这是client：初次连接'
}
this.websocketsend(JSON.stringify(data))
},
websocketonerror () { // 连接建立失败重连

this.$vux.toast.show({
text: 'WebSocket连接失败',
position: 'middle',
type: 'cancel'
})
},
websocketonmessage (e) { // 数据接收
console.log('数据接收' + e.data)
let res = JSON.parse(e.data)

if (res.code === 1) {
console.log('修改成功', res.data)
this.tablesGeted = res.data
this.$vux.toast.show({
text: '修改成功',
position: 'middle',
type: 'success'
})
} else if (res.code === 0) {
console.log('修改失败', res.data)
this.$vux.toast.show({
text: '修改失败',
position: 'middle',
type: 'cancel'
})
} else if (res.code === 2) {
console.log('获取桌表成功', res.data)
this.tablesGeted = res.data
console.log(JSON.stringify(this.tables))
this.$vux.toast.show({
text: '获取桌表成功',
position: 'middle',
type: 'success'
})
} else if (res.code === 3) {
console.log('获取桌表失败', res.data)
this.$vux.toast.show({
text: '获取桌表失败',
position: 'middle',
type: 'cancel'
})
}
},
websocketsend (Data) { // 数据发送
this.websock.send(Data)
},
websocketclose (e) {  // 关闭
console.log('已关闭连接', e)
this.$vux.toast.show({
text: 'WebSocket已关闭连接',
position: 'middle',
type: 'cancel'
})
}
},
computed: {

tables: function () {
let arr = []
if (this.tablesGeted === undefined || this.tablesGeted.length === 0) { return [] }
for (let i = 0, len = this.tablesGeted.length; i < len; i++) {
let obj = {}
obj.value = this.tablesGeted[i].tablenum
obj.state = this.tablesGeted[i].state
obj.capacity = this.tablesGeted[i].capacity
arr.push(obj)
}
return arr
},
demo22: function () {
let arr = []
if (this.tablesGeted === undefined || this.tablesGeted.length === 0) { return [] }
for (let i = 0, len = this.tablesGeted.length; i < len; i++) {
if (this.tablesGeted[i].state === true) {
let obj = {}
obj.value = this.tablesGeted[i].tablenum
obj.state = this.tablesGeted[i].state
obj.capacity = this.tablesGeted[i].capacity
arr.push(obj)
}
}
return arr
},
key () {
return this.$route.name !== undefined
? this.$route.name
: 'view-' + +new Date()
},
...mapGetters({
layoutIndex: 'layoutIndex'
})
},
created () {
console.log('created')
this.initWebSocket()
},
data () {
return {
confirmItem: {},
tablesGeted: [],
websocket: null,
showConfirm: false,
show: false
}
},
destroyed () {
this.websock.close() 
}
}



