
import ViewBox from 'vux/src/components/view-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import { mapActions } from 'vuex'
import { doLogin } from '../api/index.js'

export default {
name: 'Login',
components: {
ViewBox, XInput, Group, XButton, Cell, Divider
},
data () {
return {
showLoading: false,
form: {
username: '55160111',
password: '55160111'
},
be2333: function (value) {
return {
valid: value === '2333',
msg: 'Must be 2333'
}
}

}
},
methods: {
goto_register () {
this.$router.push({ path: '/register' })
},
...mapActions(['createLoginInfo']),
async doLogin () {
console.log('dologin')

if (this.form.username === '' || this.form.password === '') {
this.$vux.toast.show({
text: '请正确填写用户名和密码！',
position: 'middle',
type: 'cancel'
})
return
}
try {
console.log(this.form.username)
const response = await doLogin({
userType: 'waiter',
phoneNum: this.form.username,
password: this.form.password
})
console.log(response.data)
if (response.data.code === 1) {
this.createLoginInfo({
id: response.data.data.waiter_userid,
data: response.data.data
})
this.$vux.toast.show({
text: response.data.message,
position: 'middle',
type: 'success'
})
this.$router.push({ path: '/home' })
} else {
this.$vux.toast.show({
text: response.data.message,
position: 'middle',
type: 'cancel'
})
}
} catch (err) {
console.error(err)
this.$vux.toast.show({
text: '连接服务器失败',
position: 'middle',
type: 'cancel'
})
}




















}
}
}



